<template>
		<div class="header_nav">
			<div>
        <p>嗨~欢迎来到高校二手交易平台</p>
      </div>
			<ul>
				<li v-if="!userInfo.id">
          <div style="display: inline-block;margin-right: 10px;" @click="$router.push('/login')">您好,请登录</div>
          <div style="display: inline-block"  @click="$router.push('/login?type=1')">免费注册</div>
				</li>
				<li v-else>
					<div v-if="userInfo.user_nickname">您好,{{ userInfo.user_nickname }}</div>
          <div v-else>您好,{{ userInfo.user_name | nameFormat }}</div>
					<div @click="headerLogout">退出登录</div>
				</li>
        <li v-if="this.$route.path.indexOf('/home') === -1"><div @click="$router.push('/home')">返回首页</div></li>
				<li><div @click="goMe">个人中心</div></li>
				<li><div @click="goAdmin">管理员通道</div></li>
			</ul>
		</div>
</template>

<script>
  import {mapState} from 'vuex';
  import {mapActions} from 'vuex'
  import { MessageBox } from 'element-ui';

  export default {
    data(){
      return{
      }
    },
    computed: {
        ...mapState(["userInfo"])
    },
    methods:{
      ...mapActions(["logOut"]),

      handleAreaChange(value) {
        //console.log(this.selectedOptions);
      },
      headerLogout(){
        this.$confirm('您确定退出登录吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$message({
          type: 'success',
          message: '退出成功!'
          });
          let result = this.logOut({});
          localStorage.removeItem("userInfo");
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消退出'
          });
        });
      },
      goMe(){
        if(this.userInfo.id){
          this.$router.push('/me');
        }else{
          MessageBox({
            type: 'info',
            message: "请先登录!",
			      showClose: true,
          });
        }
      },
      goAdmin(){
        let result = localStorage.getItem("adminInfo");
        if(result){
          this.$router.push('/admin');
        }else{
          this.$router.push('/adminlogin');
        }
      }
    },
  }
</script>

<style scoped>
/*头部导航*/
.header_nav{
	width: 100%;
	height: 50px;
	background: rgb(49, 49, 49);
	font-family:  "Microsoft YaHei";

  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header_nav>div{
  display: flex;
  align-items: center;
}
.header_nav>div p{
	margin: 0 30px 0 20px;
	color: rgb(255, 255, 255);
	font-size: 15px;
}
.locationWrapper{
  position: relative;
  max-width: 100px;

  display: flex;
  align-items: center;

}
.locationWrapper .el-cascader{
  position: absolute;
  opacity: 0;
}
.locationWrapper .address{
  max-width: 58px;
  font-size: 15px;
  color: rgb(0, 0, 0);
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  vertical-align: middle;
}
.locationWrapper .icon{
  margin: 0 3px;
  width: 15px;
  height: 15px;
  vertical-align: middle;
}
.locationWrapper .icon path{
  fill: #dd6161;
}
.header_nav>ul{
	margin: 0 50px;
	list-style: none;

  display: flex;
  align-items: center;
}
.header_nav>ul>li:first-child{
	font-size: 14px;
	color:red;
	line-height: 20px;
	cursor: pointer;
}
.header_nav>ul>li>div{
	display: inline-block;
	padding: 5px 15px;
	font-size: 15px;
	line-height: 20px;
	color: rgb(255, 255, 255);
	text-decoration: none;
	cursor: pointer;
}
.header_nav>ul>li>div:hover{
	color: red;
}
.header_nav>ul>li:first-child>div:nth-child(2){
	padding-left: 0;
	color: red;
}
</style>



